<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process240"></a>

<p><br></p>
<p><span class="rvts17">无名岛（</span><span class="rvts50">The Nameless Island</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts21">七所祭坛的具体位置</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到进入神学院的方法，进入神学院，并想办法打开王者竞技场的大门</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><span class="rvts21">该支线以调查无名岛并进入学院为终结。可以调查的无名岛除了黑环与魔导师的战斗外，还有七所祭坛。</span>
<br><span class="rvts21">以下是七所祭坛所在位置，注意，一些祭坛在寺庙或山洞中，一些则是在无名岛的某些废墟中。</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1346.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">1. </span><span class="rvts21">人类（瑞雷克，</span><span class="rvts20">Phalic</span><span class="rvts21">）祭坛：太阳，位于</span><span class="rvts20">X:201,Y:826</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1347.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">2. </span><span class="rvts21">矮人（杜娜，</span><span class="rvts20">Duna</span><span class="rvts21">）祭坛：月亮，位于矮人神殿中，矮人神殿的位置在</span><span class="rvts20">X:325,Y:748</span><span class="rvts21">。矮人神殿中有一名被恶魔占据的杜娜骑士，打败她，并与她的灵魂交谈。（参考支线</span><span class="rvts20"></span><span class="rvts21">看守者的怜悯</span><span class="rvts20"></span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1348.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">3. </span><span class="rvts21">小鬼（赞提撒，</span><span class="rvts20">Xantessa</span><span class="rvts21">）祭坛：月亮，位于小鬼神殿中，进入该神殿需要与红宝石对话才能到达，红宝石的位置在</span><span class="rvts20">X:275,Y:824</span><span class="rvts21">，红宝石有两只狼看守着。（参考支线</span><span class="rvts20"></span><span class="rvts21">像钟表一样奔跑</span><span class="rvts20"></span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1349.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">4. </span><span class="rvts21">精灵（泰</span><span class="rvts20"></span><span class="rvts21">森迪刘斯，</span><span class="rvts20">Tir-cend</span><span class="rvts21">）祭坛：月亮，位于位置</span><span class="rvts20">X:268,Y:944</span><span class="rvts21">。该祭坛处有一只</span><span class="rvts20"></span><span class="rvts21">最后的丛林虎</span><span class="rvts20"></span><span class="rvts21">看守，据了解，无论使用什么方法，都将导致与它发生战斗。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1350.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">5. </span><span class="rvts21">巫师（阿玛蒂亚，</span><span class="rvts20">Armadia</span><span class="rvts21">）祭坛：月亮，位于阿玛蒂亚神殿中，阿玛蒂亚神殿的位置在</span><span class="rvts20">X:112,Y:737</span><span class="rvts21">，顺着蔓梯往上爬，就可以到达云霄中的阿玛蒂亚神殿。（参考支线</span><span class="rvts20"></span><span class="rvts21">冲上云霄</span><span class="rvts20"></span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1351.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">6. </span><span class="rvts21">蜥蜴（祖尔</span><span class="rvts20"></span><span class="rvts21">斯提萨，</span><span class="rvts20">Zorl Stissa</span><span class="rvts21">）祭坛：太阳，位于</span><span class="rvts20">X:198,Y:683</span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1352.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts86">7. </span><span class="rvts21">兽人（维罗格，</span><span class="rvts20">Vrogir</span><span class="rvts21">）祭坛：太阳，位于兽人神殿，兽人神殿位置</span><span class="rvts20">X:109,Y:898</span><span class="rvts21">，这座神殿在初期是有黑环看守的，需要降低水位才能浮现出来。（参考支线</span><span class="rvts20"></span><span class="rvts21">被淹没的神庙</span><span class="rvts20"></span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1353.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">不走神学院大门，可以通过一处较为偏远地方的一个隐藏洞穴，进入学院的后门。</span>
<br><span class="rvts21">该洞穴位于无名岛东部荒芜地带，经历此处需要穿越很多悬崖陡壁和亡灵火焰。角色需要使用跳跃，飞翔等技能才能达到。具体位置在</span><span class="rvts20">X:378,Y:695</span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



